import { css } from 'styled-components';

/* styles common to all buttons */
export const inputBase = css`
    height: 30px;
    border-radius: 5px;
    border: 1px solid lightgray;
    outline: none;
`;

/* size variants */
export const sm = css`
    width: 250px;
`;
export const md = css`
    width: 300px;
`;

export const lg = css`
    width: 350px;
`;

export const xl = css`
    width: 400px;
`;